.button {
  background: var(--grey);
  color: inherit;
  text-decoration: none;
  font-family: var(--font-headline);
  height: 56px;
  padding: 0 36px;
  display: flex;
  font-size: inherit;
  align-items: center;

  border: 2px solid white;
  justify-content: center;
}

  .button:focus {
    background: white;
    color: var(--starry-sky);
    outline: none;
  }
