@import url('https://fonts.googleapis.com/css?family=Fira+Sans:300,400,500,600,700');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,700');

:root {
  --font-headline: 'Playfair Display';
  --starry-sky: rgba(36, 38, 43, 1);
  --petrol: rgba(42, 56, 62, 1);
  --grey: rgba(87, 90, 99, 1);
  --blue: rgba(45, 61, 106, 1);
  --blueLight: rgba(115, 151, 255, 1);
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-family: 'Fira Sans';
  background-color: var(--starry-sky);
  background-image: url('/static/planets.png');
  background-attachment: fixed;
  color: white;
}

.frame {
  padding: 16px;
}

@media (min-width: 800px) {

.frame {
    padding: 32px;
    max-width: 800px;
    margin: 0 auto
}
  }

.logo {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  color: white;
  text-decoration: none;
}

@media (min-width: 800px) {

.logo {
    font-size: 24px
}
  }

.logoContainer {
  display: flex;
  align-items: center;
}
.rocket {
  margin-right: 16px;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  height: 50px;
}
@media (max-width: 800px) {
.rocket {
    display: none
}
  }
