.list {
  padding: 40px 0;
  list-style-type: none;
}

.item {
  font-family: var(--font-headline);
  position: relative;
  margin-bottom: 40px;
  padding-left: 40px;
  display: flex;
  align-items: center;
}

@media (min-width: 520px) {

.item {
    margin-bottom: 80px;
    font-size: 28px
}
  }

.number {
  color: var(--blue);
  font-family: 'Fira Sans';
  font-weight: bold;
  position: absolute;
  z-index: -1;
  font-size: 120px;
  left: 0;
}
