.header {
  height: 500px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  margin-bottom: 80px;
}

  @media (min-width: 520px) {.header {
    align-items: flex-start;
    height: auto;
}
  }

  @media (min-width: 900px) {.header {
    padding: 200px 0;
}
  }

.headerText {
  margin-bottom: 36px;
}

@media (min-width: 520px) {

.subline {
    font-size: 28px
}
  }
